<template>
  <Mobile>
    <div class="events page">
      <v-card flat>
        <v-app-bar app dark color="#0e0e11">
          <v-btn dark icon @click="back">
            <v-icon class="font-weight-bold">mdi-chevron-left</v-icon>
          </v-btn>
          <v-toolbar-title>MESSAGES</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items v-if="user">
            <!-- Send Message Dialog -->
            <v-row justify="center">
              <v-dialog v-model="sendDialog" persistent max-width="600px">
                <template v-slot:activator="{ on, attrs }">
                  <v-btn class="mt-4" icon dark v-bind="attrs" v-on="on">
                    <v-icon color="white">mdi-plus</v-icon>
                  </v-btn>
                </template>
                <v-card>
                  <form @submit.prevent="save" enctype="multipart/form-data">
                    <v-card-title>
                      <span class="headline">Send Message</span>
                    </v-card-title>
                    <v-divider></v-divider>
                    <v-card-text>
                      <v-text-field name="title" v-model="title" label="Title" autocomplete="off" required>
                      </v-text-field>
                      <v-textarea outlined name="message" v-model="message" label="Message" autocomplete="off" required>
                      </v-textarea>
                    </v-card-text>
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <div class="add-event mr-4">
                        <button class="btn">Save</button>
                      </div>
                      <v-btn color="blue darken-1" text @click="sendDialog = false">Close</v-btn>
                    </v-card-actions>
                  </form>
                </v-card>
              </v-dialog>
            </v-row>
          </v-toolbar-items>
        </v-app-bar>
        <Header />

        <!-- List Messages Dialog -->
        <v-row no-gutters justify="center">
          <v-col cols="12" sm="12" md="8">
            <v-card class="mb-10" elevation="0">
              <v-list subheader two-line>
                <v-text-field class="mx-2 mt-5" v-model="search" append-icon="mdi-magnify" label="Search" rounded solo
                  single-line></v-text-field>
                <div v-if="!data.length">
                  <Empty />
                </div>
                <v-list-item v-for="(message, id) in filter" :key="id">
                  <v-list-item-avatar>
                    <v-icon color="blue">mdi-android-messages</v-icon>
                  </v-list-item-avatar>

                  <v-list-item-content @click="
                    readMessage(
                      message.id,
                      message.created,
                      message.message,
                      message.title
                    )
                  ">
                    <v-list-item-title class="font-weight-medium text-capitalize">{{ message.title }}
                    </v-list-item-title>
                    <v-list-item-subtitle class="primary--text">{{
                        message.message
                    }}</v-list-item-subtitle>
                    <v-list-item-subtitle class="mt-1">
                      <span>
                        {{
                            formatDistance(new Date(message.created), new Date())
                        }}
                        ago
                      </span>
                      <span class="ml-5">
                        <v-icon color="#610018" size="16">mdi-comment-eye-outline</v-icon>
                        views:
                      </span>
                    </v-list-item-subtitle>
                  </v-list-item-content>
                  <v-list-item-action v-if="user">
                    <v-btn icon @click="deleteMessage(message)">
                      <v-icon color="#610018">mdi-delete-outline</v-icon>
                    </v-btn>
                  </v-list-item-action>
                </v-list-item>
              </v-list>
            </v-card>
          </v-col>
        </v-row>
      </v-card>
    </div>
  </Mobile>
</template>
<script>
import Mobile from "../layouts/Mobile";
import Header from "@/components/Header";
import Empty from "@/components/Empty";
import { au } from "@/firebase/init";
import axios from "axios";
import { formatDistance } from "date-fns";
export default {
  name: "Messages",
  data() {
    return {
      formatDistance,
      data: [],
      title: "",
      message: "",
      search: "",
      user: "",
      err: null,
      sendDialog: false,
      messageDialog: true,
    };
  },
  components: { Mobile, Header, Empty },

  computed: {
    filter: function () {
      const search = this.search.toLowerCase();
      if (!search) return this.data;

      return this.data.filter((message) => {
        const text = message.get("title").toLowerCase();
        return text.indexOf(search) > -1;
      });
    },
  },

  created() {
    au.onAuthStateChanged((user) => {
      if (user) {
        this.user = user;
      } else {
        this.user = null;
      }
    });
    this.getData();
  },

  methods: {
    async save() {
      let data = new FormData();
      data.append("title", this.title);
      data.append("message", this.message);
      axios
        .post(
          "http://localhost:8888/seaviewprimary/src/api/api.php?action=addMessage",
          data
        )
        .then((res) => {
          this.title = "";
          this.message = "";
          this.getData();
          if (res.data.error) {
            console.log(res.data);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },

    async getData() {
      axios
        .post(
          "http://localhost:8888/seaviewprimary/src/api/api.php?action=getMessage"
        )
        .then((res) => {
          console.log(res.data.user_Data);
          this.data = res.data.user_Data;
        })
        .catch((err) => {
          console.log(err);
        });
    },

    async deleteMessage() { },

    async readMessage(id, created, message, title) {
      this.$router.push({
        name: "Message",
        params: {
          id: id,
          title: title,
          message: message,
          created: created,
        },
      });
    },

    back() {
      window.history.back();
    },
  },
};
</script>
